import styles from './Services.module.css';
const EasySite = '/images/easySite.svg';
const HardSite = '/images/hardSite.svg';
const OnlineStore = '/images/onlineStore.svg';
const TildaSite = '/images/tildaSite.svg';
const Clock = '/images/clock.svg';

const Services = () => {
    const services = [
        {
            icon: EasySite,
            title: ' ',
            description: '  ,       ',
            term: ' 4 '
        },
        {
            icon: HardSite,
            title: ' ',
            description: '    :  ,        ',
            term: ' 4 '
        },
        {
            icon: OnlineStore,
            title: '  Tilda',
            description: '   Tilda,  ,    ',
            term: ' 4 '
        },
        {
            icon: TildaSite,
            title: '-',
            description: '       ,        ',
            term: ' 4 '
        }
    ];

    const scrollToContacts = () => {
        const contactsSection = document.getElementById('contacts');
        if (contactsSection) {
            contactsSection.scrollIntoView({ behavior: 'smooth' });
        }
    };

    return (
        <section id="services" className={styles.serviceSection}>
            <div className="container">
                <h2 className={styles.sectionTitle}>   </h2>
                <div className={styles.servicesGrid}>
                    {services.map((service, index) => (
                        <div key={index} className={styles.serviceCard}>
                            <div className={styles.icon}>
                                <img src={service.icon} alt={service.title} />
                            </div>
                            <h3 className={styles.serviceTitle}>{service.title}</h3>
                            <p className={styles.serviceDescription}>{service.description}</p>
                            <div className={styles.period}>
                                <img src={Clock} alt=" " className={styles.clockIcon} />
                                <p className={styles.serviceTerm}>{service.term}</p>
                            </div>
                            <button
                                className={styles.serviceBtn}
                                onClick={scrollToContacts}
                            >
                                 
                            </button>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Services;