import { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import styles from './Contacts.module.css';

const Contacts = () => {
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [submitStatus, setSubmitStatus] = useState(null);
    const [botStatus, setBotStatus] = useState('checking');
    const [chatId, setChatId] = useState(() => {
        const saved = localStorage.getItem('telegram_chat_id');
        return saved ? saved : null;
    });

    const {
        register,
        handleSubmit,
        reset,
        formState: { errors }
    } = useForm();

    const BOT_CONFIG = {
        TOKEN: '7983672152:AAG2_woAj45A1lr3at9JWrlC5zlatAIGe1c',
        USERNAME: 'ZaverstkaBot',
        CHAT_ID: '5572376646'
    };

    useEffect(() => {
        const setupBot = async () => {
            try {
                const meResponse = await fetch(`https://api.telegram.org/bot${BOT_CONFIG.TOKEN}/getMe`);
                const meData = await meResponse.json();

                if (!meData.ok) {
                    setBotStatus('invalid_token');
                    console.error('  ');
                    return;
                }

                console.log('  :', meData.result.username);

                if (chatId) {
                    console.log('   Chat ID:', chatId);
                    setBotStatus('ready');
                    return;
                }

                const updatesResponse = await fetch(`https://api.telegram.org/bot${BOT_CONFIG.TOKEN}/getUpdates`);
                const updatesData = await updatesResponse.json();

                if (updatesData.ok && updatesData.result.length > 0) {
                    const lastUpdate = updatesData.result[updatesData.result.length - 1];
                    const newChatId = lastUpdate.message.chat.id.toString();

                    setChatId(newChatId);
                    localStorage.setItem('telegram_chat_id', newChatId);

                    console.log(' Chat ID   :', newChatId);
                    setBotStatus('ready');
                } else {
                    console.log('   ');

                    if (BOT_CONFIG.CHAT_ID) {
                        console.log('   Chat ID  ');
                        setChatId(BOT_CONFIG.CHAT_ID);
                        localStorage.setItem('telegram_chat_id', BOT_CONFIG.CHAT_ID);
                        setBotStatus('ready');
                    } else {
                        setBotStatus('no_messages');
                    }
                }

            } catch (error) {
                console.error('  :', error);
                if (BOT_CONFIG.CHAT_ID) {
                    setChatId(BOT_CONFIG.CHAT_ID);
                    setBotStatus('ready');
                } else {
                    setBotStatus('error');
                }
            }
        };

        setupBot();
    }, []);

    const sendToBot = async (data) => {
        const currentChatId = chatId || BOT_CONFIG.CHAT_ID;

        if (!currentChatId) {
            throw new Error('  Chat ID  ');
        }

        const message = `
 <b>   </b>

 <b>:</b> ${data.name}
 <b>:</b> ${data.contactMethod}
 <b>:</b>
${data.projectDescription}

 <b>:</b> ${new Date().toLocaleString('ru-RU')}
 <b>:</b> zaverstka.ru
        `;

        const response = await fetch(`https://api.telegram.org/bot${BOT_CONFIG.TOKEN}/sendMessage`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                chat_id: currentChatId,
                text: message,
                parse_mode: 'HTML'
            })
        });

        return await response.json();
    };

    const handleEmailClick = () => {
        window.location.href = 'mailto:zaverstka@gmail.com?subject=   zaverstka.ru&body=!     ...';
    };

    const onSubmit = async (data) => {
        setIsSubmitting(true);
        setSubmitStatus(null);

        try {
            const currentChatId = chatId || BOT_CONFIG.CHAT_ID;

            if (!currentChatId) {
                throw new Error('   ');
            }

            const result = await sendToBot(data);

            console.log(' :', result);

            if (result.ok) {
                setSubmitStatus({
                    type: 'success',
                    message: ' !',
                    details: '      .',
                    timestamp: new Date().toLocaleTimeString()
                });
                reset();
            } else {
                console.error(' :', result);

                setSubmitStatus({
                    type: 'error',
                    message: ' ',
                    details: ',      Telegram  WhatsApp'
                });
            }

        } catch (error) {
            console.error(' :', error);

            setSubmitStatus({
                type: 'error',
                message: ' ',
                details: error.message || '     '
            });

        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <section id="contacts" className={styles.contacts}>
            <div className="container">
                <div className={styles.contactsContent}>
                    <div className={styles.contactInfo}>
                        <h4></h4>
                        <p className={styles.welcomeText}>  !</p>
                        <p className={styles.contactDescription}>
                                    
                        </p>

                        <div className={styles.directContacts}>
                            <div className={styles.contactMethods}>
                                <a
                                    href="https://t.me/Ttwinkleee"
                                    className={styles.contactButton}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    <strong>Telegram</strong>
                                </a>
                                <a
                                    href="https://wa.me/89805447999"
                                    className={styles.contactButton}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    <strong>WhatsApp</strong>
                                </a>
                                <button
                                    className={styles.contactButton}
                                    onClick={handleEmailClick}
                                    type="button"
                                >
                                    <strong>Email</strong>
                                </button>
                            </div>
                        </div>
                    </div>

                    <div className={styles.formInfo}>
                        <form onSubmit={handleSubmit(onSubmit)} className={styles.form}>
                            <div className={styles.formGroup}>
                                <label className={styles.fieldLabel}></label>
                                <input
                                    type="text"
                                    placeholder="   "
                                    {...register("name", {
                                        required: "  ",
                                        minLength: {
                                            value: 2,
                                            message: " 2 "
                                        },
                                        maxLength: {
                                            value: 50,
                                            message: " 50 "
                                        }
                                    })}
                                    disabled={isSubmitting}
                                />
                                {errors.name && (
                                    <span className={styles.fieldError}>{errors.name.message}</span>
                                )}
                            </div>

                            <div className={styles.formGroup}>
                                <label className={styles.fieldLabel}> </label>
                                <input
                                    type="text"
                                    placeholder="@Telegram,  , email"
                                    {...register("contactMethod", {
                                        required: " ",
                                        minLength: {
                                            value: 3,
                                            message: " 3 "
                                        }
                                    })}
                                    disabled={isSubmitting}
                                />
                                {errors.contactMethod && (
                                    <span className={styles.fieldError}>{errors.contactMethod.message}</span>
                                )}
                            </div>

                            <div className={styles.formGroup}>
                                <label className={styles.fieldLabel}> </label>
                                <textarea
                                    rows="6"
                                    placeholder="   , , ,   ..."
                                    {...register("projectDescription", {
                                        required: "  ",
                                        minLength: {
                                            value: 20,
                                            message: " 20 "
                                        },
                                        maxLength: {
                                            value: 2000,
                                            message: " 2000 "
                                        }
                                    })}
                                    disabled={isSubmitting}
                                ></textarea>
                                {errors.projectDescription && (
                                    <span className={styles.fieldError}>{errors.projectDescription.message}</span>
                                )}
                            </div>

                            <div className={styles.formGroup}>
                                <label className={styles.checkbox}>
                                    <input
                                        type="checkbox"
                                        {...register("agreement", {
                                            required: "  "
                                        })}
                                        disabled={isSubmitting}
                                    />
                                    <span className={styles.checkboxBox}></span>
                                    <span className={styles.checkboxText}>
                                           {' '}
                                        <Link
                                            to="/privacy-policy"
                                            className={styles.privacyLink}
                                        >
                                             
                                        </Link>{' '}
                                    </span>
                                </label>
                                {errors.agreement && (
                                    <span className={styles.fieldError}>{errors.agreement.message}</span>
                                )}
                            </div>

                            {submitStatus && (
                                <div className={`${styles.message} ${styles[submitStatus.type]}`}>
                                    <div className={styles.messageHeader}>
                                        <span className={styles.messageIcon}>
                                            {submitStatus.type === 'success' ? '' :
                                                submitStatus.type === 'error' ? '' : ''}
                                        </span>
                                        <div>
                                            <strong>{submitStatus.message}</strong>
                                            <p className={styles.messageText}>{submitStatus.details}</p>
                                            {submitStatus.timestamp && (
                                                <small className={styles.timestamp}>
                                                      {submitStatus.timestamp}
                                                </small>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            )}

                            <button
                                type="submit"
                                className={`${styles.submitButton} ${isSubmitting ? styles.loading : ''}`}
                                disabled={isSubmitting || botStatus !== 'ready'}
                            >
                                {isSubmitting ? (
                                    <>
                                        <span className={styles.buttonSpinner}></span>
                                        ...
                                    </>
                                ) : (
                                    <>
                                         
                                    </>
                                )}
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Contacts;